<template>
    <div>
        <el-row>
            <el-col :span="13" style="padding:10px;">
                <el-row>
                    <el-col :span="24">
                        <el-form style="display: flex;align-items: center;justify-content: center;" label-width="20px">
                            <el-form-item>
                                <el-date-picker
                                v-model="fromDateToDate"
                                type="datetimerange"
                                :picker-options="pickerOptions"
                                range-separator=""
                                start-placeholder=""
                                end-placeholder="">
                                </el-date-picker>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="searchStudentScores"></el-button>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="exportToExcel" :disabled="studentScores.length < 1"></el-button>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="24">
                        <el-table :data="studentScores" style="width: 100%" v-if="studentScores[0]" id="table-out">
                            <el-table-column label="" fixed="left">
                                <template slot-scope="scope">
                                    {{ scope.row.studentName }}
                                </template>
                            </el-table-column>
                            <el-table-column  
                            :label="timestampToString(studentScores[0].examPaperAllocations[index].examPaperRelease.examEndTime)" 
                            v-for="(examPaperAllocation,index) in studentScores[0].examPaperAllocations" :key="index">
                                <template slot-scope="scope">
                                    {{  !scope.row.examPaperAllocations[index]?'':
                                        scope.row.examPaperAllocations[index].score?scope.row.examPaperAllocations[index].score.score:'' }}
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-col>
                </el-row>
            </el-col>
            <el-col :span="11" style="padding:10px;">
                <h2></h2>
                <!--  -->
                <el-table :data="students" style="width: 100%" @selection-change="handleSelectionChange"
                ref="table">
                <el-table-column type="selection" width="55"/>
                <el-table-column label="ID" prop="studentId" with="300"/>
                <el-table-column prop="studentName" label="" width="120"/>
                <el-table-column label="" width="120">
                    <template slot-scope="scope">
                        {{ scope.row.administrator.administratorName }}
                    </template>
                </el-table-column>
                </el-table>    
                <el-button @click="toggleSelection()" style="margin: 20px;"></el-button>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import api from '@/axios/index'
import FileSaver from 'file-saver'
import * as XLSX from 'xlsx'
export default{
    data(){
        return{
            students:[],
            studentsSelected:[],
            fromDateToDate:[],
            pickerOptions: {
            shortcuts: [
            {
                text: '',
                onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24);
                    picker.$emit('pick', [start, end]);
                }
            },
            {
            text: '',
                onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                    picker.$emit('pick', [start, end]);
                }
            }, {
                text: '',
                onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                picker.$emit('pick', [start, end]);
                }
            }, {
                text: '',
                onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                picker.$emit('pick', [start, end]);
                }
            }]},
            studentScores:[
            ]
        }
    },
    methods:{
        handleSelectionChange(val){
            this.studentsSelected=val
        },
        toggleSelection(rows) {
            if (rows) {
            rows.forEach(row => {
                this.$refs.table.toggleRowSelection(row);
            });
            } else {
            this.$refs.table.clearSelection();
            }
            this.studentsSelected = [ ];
        },
        searchStudentScores(){
            // 
            if(!this.fromDateToDate[0] || !this.fromDateToDate[1]){
                this.$message.error("")
                return
            }
            // 
            if(this.fromDateToDate[0] > this.fromDateToDate[1]){
                this.$message.error("")
                return
            }
            // 
            if(this.studentsSelected.length < 1){
                this.$message.error("")
                return
            }
            api.getStudentScoreListByStudentsNew(this.studentsSelected, this.fromDateToDate[0], this.fromDateToDate[1]).then(res => {
                // 
                const data = res.data;

                //  examPaperAllocations 
                //  examPaperAllocations 
                const sortedData = data.map(student => {
                    //  examPaperAllocations 
                    if (!Array.isArray(student.examPaperAllocations)) {
                        student.examPaperAllocations = [];
                    }
                    return student;
                }).sort((b,a) => {
                    //  b  examPaperAllocations  a  b 
                    if (b.examPaperAllocations.length > a.examPaperAllocations.length) {
                        return -1;
                    }
                    //  a  examPaperAllocations  b  a 
                    if (a.examPaperAllocations.length > b.examPaperAllocations.length) {
                        return 1;
                    }
                    // 
                    return 0;
            });

            //  this.studentScores
            this.studentScores = sortedData;
        });
        },
        exportToExcel() {
            // 
            if (!this.studentScores || this.studentScores.length === 0) {
                this.$message.warning("");
                return;
            }

            // 1. 
            const header = [""];
            if (this.studentScores[0].examPaperAllocations) {
                this.studentScores[0].examPaperAllocations.forEach((allocation) => {
                header.push(this.timestampToString(allocation.examPaperRelease.examEndTime));
                });
            }

            // 2. 
            const data = [header]; // 
            this.studentScores.forEach((student) => {
                const row = [student.studentName];
                if (student.examPaperAllocations) {
                student.examPaperAllocations.forEach((allocation) => {
                    row.push(allocation.score ? allocation.score.score : "");
                });
                }
                data.push(row);
            });

            // 3. 
            const worksheet = XLSX.utils.aoa_to_sheet(data);

            // 4. 
            const workbook = XLSX.utils.book_new();
            XLSX.utils.book_append_sheet(workbook, worksheet, "");

            // 5.  Excel 
            const excelBuffer = XLSX.write(workbook, {
                bookType: "xlsx",
                type: "array",
            });
            const blob = new Blob([excelBuffer], {
                type: "application/octet-stream",
            });
            FileSaver.saveAs(blob, ".xlsx");
        },
        timestampToString(timestamp) {
            const date = new Date(timestamp);
            const year = date.getFullYear();
            const month = String(date.getMonth() + 1).padStart(2, '0'); // 0
            const day = String(date.getDate()).padStart(2, '0');
            const hours = String(date.getHours()).padStart(2, '0');
            const minutes = String(date.getMinutes()).padStart(2, '0');
            const seconds = String(date.getSeconds()).padStart(2, '0');
            return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
        }
    },
    mounted(){
        api.getAllStudent().then(res=>{
            this.students = res.data
        })
    }
}
</script>